import React, { Component } from 'react';
import ApexCharts from 'apexcharts';
import ReactApexChart from 'react-apexcharts';

class ApexChart extends React.Component {
  static defaultProps = {
    series: '90'
    // lables: 'percent'
  };

  constructor(props) {
    super(props);
    this.state = {
      series: [70],
      options: {
        chart: {
          height: 350,
          type: 'radialBar'
        },
        plotOptions: {
          radialBar: {
            hollow: {
              size: '70%'
            }
          }
        },
        labels: [this.props.labels]
      }
    };
  }

  render() {
    const { series, labels } = this.props;
    return (
      <div id="chart">
        <ReactApexChart options={this.state.options} series={[series]} labels={labels} type="radialBar" height={200} />
      </div>
    );
  }
}

export default ApexChart;
